import { motion } from "framer-motion";
import { NavLink } from "react-router-dom";
import styled from "styled-components";
import { Github } from "../components/AllSvgs";
import { mediaQueries } from "../components/Themes";
import "@fontsource/anonymous-pro" // Defaults to weight 400.

const Box = styled(motion.li)`
  width: 20rem;
  height: 50vh;
  background-color: ${(props) => props.theme.text};
  color: ${(props) => props.theme.body};
  padding: 1.5rem 2rem;
  margin-right: 8rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border: 1px solid ${(props) => props.theme.body};

  transition: all 0.2s ease;
  &:hover {
    background-color: ${(props) => props.theme.body};
    color: ${(props) => props.theme.text};
    border: 1px solid ${(props) => props.theme.text};
  }

  ${mediaQueries(50)`
        width:16rem;
        margin-right:6rem;
        height:35vh;
       

  `};
  ${mediaQueries(40)`
        width:14rem;
        margin-right:4rem;
        height:35vh;
        
        

  `};
  ${mediaQueries(25)`
        width:12rem;
        margin-right:4rem;
        height:35vh;
        padding:1.5rem 1.5rem;
        
        

  `};
  ${mediaQueries(20)`
        width:10rem;
        margin-right:4rem;
        height:40vh;

        
        

  `};
`;

const Title = styled.h4`
  padding-bottom: 0.5rem;
  font-size: calc(1em + 0.5vw);
  font-family: "Anonymous Pro";


`;
const Description = styled.h4`
font-family: "Anonymous Pro";

  font-size: calc(0.8em + 0.1vw);
  font-weight: 500;
  border-bottom: 2px solid ${(props) => props.theme.body};
  padding-bottom: 0.5rem;
  ${Box}:hover & {
    border-bottom: 2px solid ${(props) => props.theme.text};
  }

  ${mediaQueries(25)`
  font-size:calc(0.7em + 0.3vw);



  `};
  ${mediaQueries(20)`
  font-size:calc(0.6em + 0.3vw);



  `};
`;

const Description2 = styled.h4`
font-family: "Anonymous Pro";

  font-size: calc(0.8em + 0.3vw);
  font-weight: 500;

  ${mediaQueries(25)`
  font-size:calc(0.7em + 0.3vw);



  `};
  ${mediaQueries(20)`
  font-size:calc(0.6em + 0.3vw);



  `};
`;

const Tags = styled.div`
  padding-top: 0.5rem;
  display: flex;
  flex-wrap: wrap;
  ${Box}:hover & {
    border-top: 2px solid ${(props) => props.theme.text};
  }
`;
const Tag = styled.span`
  margin-right: 1rem;
  font-size: calc(0.8em + 0.3vw);

  ${mediaQueries(25)`
  font-size:calc(0.7em);


  `};
`;
const Footer = styled.footer`

  display: flex;
  justify-content: space-evenly;
`;
const Link = styled(NavLink)`
  background-color: ${(props) => props.theme.body};
  color: ${(props) => props.theme.text};
  text-decoration: none;
  padding: 0.5rem calc(2rem + 2vw);
  font-size: calc(1em + 0.5vw);

  ${Box}:hover & {
    background-color: ${(props) => props.theme.text};
    color: ${(props) => props.theme.body};
  }
`;
const Git = styled(NavLink)`
  color: inherit;
  text-decoration: none;

  ${Box}:hover & {
    & > * {
      fill: ${(props) => props.theme.text};
    }
  }
`;

const item = {
  hidden: { scale: 0 },
  show: { scale: 1, transition: { type: "spring", duration: 0.5 } },
};
//const tags = ["react","gsap","javascript"]
const Card = (props) => {
  const { id, name, employer, description, additional, period, site, github } = props.data;
  return (
    <Box key={id} variants={item}>
      
      <Description><Title>{name}</Title><br></br>{employer}</Description>
      <Description2>{period}</Description2>

      <Description2></Description2>
      <Description2></Description2>
      <Description2></Description2>
      <Description2></Description2>


      <Description2>{description}</Description2>
      <Description2></Description2>

      <Description2>{additional}</Description2>

      <Description2></Description2>
      <Description2></Description2>
      <Description2></Description2>
      <Description2></Description2>
      <Description2></Description2>
      <Description2></Description2>
      <Description2></Description2>
      <Description2></Description2>
      <Description2></Description2>
      <Description2></Description2>
      <Description2></Description2>


      <Footer>
        <Link to={{ pathname: `${site}` }} target="_blank">
          Visit Site
        </Link>
      </Footer>
    </Box>
  );
};

export default Card;
